.vertical-nav {
    position: fixed;
    right: 40px;
    top: 50%;
    bottom: auto;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    z-index: 1;
}

.vertical-nav li 
{
    list-style: none;
    padding-bottom: 5px;
}

.vertical-nav li a 
{
    font-size: 16px;
}

.vertical-nav li .active 
{
    font-size: 18px;
    font-weight: 700;
}
